<template>
  <v-container
    id="Mailer"
    fluid
    tag="section"
  >
    <v-card
    elevation="3"
    shaped
    >
      <v-toolbar
      flat
      color="primary"
      >
        <v-icon color="white">mdi-archive</v-icon>
        <v-toolbar-title class="font-weight-light text-white mx-1">
          Mail Header
        </v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>
      <v-row
        align="center"
        class=""
      >
        <v-col
        cols="10"
        class="mx-auto"
        >
          <v-combobox
            :items="items"
            v-model="kepada"
            @keyup.space="kep"
            outlined
            dense
            small-chips
            label="Kepada"
            multiple
          ></v-combobox>
        </v-col>
        <v-col
        cols="10"
        class="mx-auto -mt-11"
        >
          <v-text-field
            outlined
            v-model="subjek"
            dense
            chips
            small-chips
            label="Subjek"
          >

          </v-text-field>
        </v-col>
      </v-row>
    </v-card>
    <v-card
    elevation="3"   
    class="mt-3 rounded-tl-xl"
    >
    <v-toolbar
    flat
    color="primary"
    >
      <v-icon color="white">mdi-archive</v-icon>
      <v-toolbar-title class="font-weight-light mx-1 text-white hidden md:block">
        Mail Body
      </v-toolbar-title>
      <v-spacer>
      <div class="bg-white bg-opacity-50 rounded-l-md shadow-md grid grid-flow-col  md:w-1/2 md:mx-auto divide-x gap-1 text-xl">
        <div class="p-2 vx transition-all ease-in-out duration-200 hover:bg-sky-600  hover:bg-opacity-100 hover:text-white flex" title="Lampirkan Image" @click="gambar"><i class="fa-solid fa-camera"></i> <span class="mx-2 hidden md:block text-sm flex ">Gambar</span></div>
        <div class="p-2 vx transition-all ease-in-out duration-200 hover:bg-violet-600  hover:bg-opacity-100 hover:text-white flex" title="Lampirkan File" @click="file"><i class="fa-solid fa-paperclip"></i> <span class="mx-2 hidden md:block text-sm flex">File</span></div>
        <div class="p-2 vx transition-all ease-in-out duration-200 hover:bg-rose-600  hover:bg-opacity-100 hover:text-white flex" title="Kirimkan "><i class="fa-solid fa-paper-plane"></i> <span class="mx-2 hidden md:block text-sm flex" @click="kirim">Kirim</span></div>
      </div>
  </v-spacer>
      <v-btn
        fab
        small
        @click="edite"
      >
        <v-icon >
          mdi-pencil
        </v-icon>
      </v-btn>
    </v-toolbar>
    </v-card>
    <v-card
    elevation="3"
    class="mt-1 p-5 h-screen edit"
    v-model="text"
    />
    <div class="hidden">
      <input type="file" accept="" id="gambar">
      <input type="file" accept="" id="file">
    </div>
    <div class="p-2 rounded-lg fixed sav hidden bg-gray-200 z-20 bottom-24 right-6 shadow filter box-shadow-md  grid-flow-col gap-1"></div>
    <div class="mb-24"></div>
  </v-container>
</template>
<script>
export default {
  data(){
    return {
      kepada: [],
      subjek: '',
      }
    },
  methods:{
    edite(){
      var y = document.querySelectorAll(".edit")[0]
      if (y.contentEditable == "true") {
        y.contentEditable = "false"
      } else {
        y.contentEditable = "true"
      }
    },
    kep(){
      console.log(this.kepada)
    },
    gambar(){
      const q = document.querySelectorAll("#gambar")[0]
      const y = document.querySelectorAll('.sav')[0]
      q.click()
      q.addEventListener("input", () => {
        if (q.files.length == 1) {
          var a = q.files[0].name
          y.style.display = "grid"
          y.insertAdjacentHTML("afterbegin", `
          <div class="lem text-lg bg-white" title="${a}">
            <button class="p-2 shadow-md rounded-lg"><i class="fa-solid fa-image"></i></button>
          </div>
          `)
          var lem = document.querySelectorAll(".lem")
          lem.forEach((item, i) => {
            lem[i].addEventListener("click", () => {
              lem[i].remove()
            })
          })
        }
      })
    },
    file(){
      const q = document.querySelectorAll("#file")[0]
      const y = document.querySelectorAll('.sav')[0]
      q.click()
      q.addEventListener("input", () => {
        if (q.files.length == 1) {
          var a = q.files[0].name
          y.style.display = "grid"
          y.insertAdjacentHTML("afterbegin", `
          <div class="lem text-lg bg-white" title="${a}">
            <button class="p-2 shadow-md rounded-lg"><i class="fa-solid fa-file"></i></button>
          </div>
          `)
          var lem = document.querySelectorAll(".lem")
          lem.forEach((item, i) => {
            lem[i].addEventListener("click", () => {
              lem[i].remove()
            })
          })
        }
      })
    },
    kirim(){
      alert("Anda tidak bisa mengirimkan email dalam mode demo")
    },
  },
  mounted(){
    

  },
}
</script>